body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #264653;
}

*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}body{box-sizing:border-box}body{font-weight:400;font-size:1.6rem;line-height:1.7}.heading-primary{color:rgba(255,255,255,0.9);line-height:1;margin-left:10rem;margin-top:12rem;width:100%}.heading-primary--pre{display:block;font-size:8rem;font-family:'Montserrat', sans-serif;font-weight:800}.heading-primary--main{font-family:'Montserrat', sans-serif;font-weight:800;display:block;font-size:20rem;margin-left:7rem;margin:4rem 0;max-width:75%;background-color:#2a9d8f;padding:5rem 5rem 10rem 5rem;box-shadow:0 0.6rem 2rem #182e36}.heading-primary--sub{display:block;font-size:2rem;margin-left:8rem;line-height:1.3;max-width:40%;padding:2.5rem;background-color:#305a6b;margin-top:-8rem;box-shadow:0 0.6rem 2rem #182e36}.emphasis{color:#e76f51}.section-heading{color:rgba(255,255,255,0.9);display:grid;line-height:1;margin-bottom:15rem}.section-heading:first-of-type{margin-top:0}.section-heading--background{grid-area:1 / 1 / 2 / 2;font-size:9rem;color:rgba(244,162,97,0.3);font-weight:200;width:50%}.section-heading--foreground{grid-area:2 / 1 / 2 / 2;color:rgba(255,255,255,0.9);margin-left:3rem;font-family:'Montserrat', sans-serif;font-weight:800}.section-heading--foreground .title{font-size:10rem;line-height:0}a{text-decoration:none;font-size:1.7rem;color:rgba(255,255,255,0.9);display:inline-block}a:after{visibility:hidden;content:"";display:block;height:0.2rem;top:0.1rem;background-color:#e76f51;position:relative;-webkit-transform:scaleX(0);transform:scaleX(0);transition:all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1) 0s}a:hover:after{visibility:visible;-webkit-transform:scaleX(1);transform:scaleX(1)}.App{text-align:center;color:rgba(255,255,255,0.9);text-align:left;margin:5rem 0}.App .content{max-width:1200px;margin:0 auto}

.icon{display:inline-block}.icon--container{text-decoration:none;display:inline-block;text-align:center;transition:all 0.1s linear 0.1s;margin:0 3rem}.icon--container:hover{color:#e76f51;-webkit-transform:translateY(-15%);transform:translateY(-15%)}.icon--container__action{display:block;margin:0.5rem auto}.icon--container:after{display:none}.icon--container:after:hover{visibility:hidden}

.socials{display:flex;justify-content:space-between;flex:1 1 auto;flex-direction:inherit;align-items:inherit}

